import TopHeader from "./components/TopHeader";
import YearTotal from "@/components/YearTotal";
import SmallRegion from "./components/SmallRegion";
import NormalRegion from "./components/NormalRegion";
import LargeRegion from "./components/LargeRegion";
import mixin from "@/mixins/index";
export default {
  components: { TopHeader, YearTotal, SmallRegion, NormalRegion, LargeRegion },
  name: "Operate",
  mixins: [mixin],
  data() {
    return {
      // 展示中型卡片
      isShowNormalCard: false,
      // 展示更多
      isShowMore: false,
      list: [],
      id: undefined,
    };
  },
  created() {
    // 区域id
    this.id = this.$route.params.id;
    this.getData();
  },
  methods: {
    onToShop(id) {
      console.log(this.$route);
      const { name } = this.$route;
      if (name === "Operate") {
        this.$router.push(`/region/1`);
      } else if (name === "Region") {
        this.$router.push(`/shop/1`);
      }
    },
    onShowDetail(index) {
      this.list = this.list.map((v, i) => ({
        ...v,
        isShowDetail: index === i ? !v.isShowDetail : false,
      }));
    },
    // 超过4个以上，默认折叠
    onToggleClick() {
      this.isShowMore = !this.isShowMore;
      if (this.isShowMore) {
        if (this.id) {
          this.list.push(
            ...[{ name: "简阳旗舰二店" }, { name: "乐山华联周大生" }]
          );
        } else {
          this.list.push(...[{ name: "成都区域" }, { name: "泸州区域" }]);
        }
      } else {
        this.list.splice(4);
      }
    },
    getData() {
      if (this.id) {
        this.list = [
          { name: "简阳旗舰二店" },
          { name: "乐山华联周大生" },
          { name: "乐山万达周大生" },
          { name: "乐山王府井周大生" },
        ];
      } else {
        this.list = [
          { name: "成都区域" },
          { name: "泸州区域" },
          { name: "成都区域" },
          { name: "泸州区域" },
        ];
      }
    },
  },
};
