import SalesCard from "@/components/SalesCard";
import LineChart from "./LineChart";
import MemberInfo from "@/components/MemberInfo";
export default {
  components: { SalesCard, LineChart, MemberInfo },
  props: {
    info: {
      type: Object,
      default: () => ({}),
    },
    index: {
      type: Number,
    },
  },
  data() {
    return {
      active: 1,
    };
  },
  computed: {},
  methods: {
    toShopPage() {
      this.$emit("shop");
    },
    handleProgressBg(index) {
      return this.active === index ? "#fff" : "";
    },
    onToggle(index) {
      this.active = index;
    },
    // 收起
    onChangeClick() {
      this.$emit("change");
    },
  },
};
